// @flow

import React from 'react';
import { useLocation } from 'react-router-dom';

function NoMatch() {
  const location = useLocation();
  return (
    <div style={{ margin: '20px' }}>
      <h3>
        No match found for
        <code>{location.pathname}</code>
      </h3>
    </div>
  );
}

export default NoMatch;
